export default {
	props: {
		
	},
	data() {
		return {
			poisList: [],
			isPoisList: false
		}
	},
	onShow () {
		this.poisList = uni.getStorageSync('diary_list');
		if (this.poisList.length == 0) {
			this.isPoisList = true;
		} else {
			this.isPoisList = false;
		}
	},
	methods: {
		toDetail (id) {
			uni.navigateTo({
				url: `../detail/detail?id=${id}`,
			})
		},
		toWrite () {
			uni.navigateTo({
				url: '../writeDiary/writeDiary'
			})
		}
	}
}
